//--------------------------------------------------------------
//Fonts
//--------------------------------------------------------------

$main-font: 'Red Ring';

//--------------------------------------------------------------
//Fonts size
//--------------------------------------------------------------

$size12: 12px;
$size13: 13px;
$main-size: 14px;
$size15: 15px;
$size16: 15px;
$size18: 18px;
$size19: 19px;
$size25: 25px;
$size35: 35px;
$size75: 75px;

//--------------------------------------------------------------
//Colors
//--------------------------------------------------------------

$white: #fff;
$black: #000;
$green: #0ED193;
$blue: #01A0BF;
$light-grey: #E4ECEE;
$grey: #ACBBBE;
$sea: #235660;


//--------------------------------------------------------------
//Extends fonts
//--------------------------------------------------------------

%h1 {
  font-family: 'Red Ring';
  font-style: normal;
  font-weight: 500;
  font-size: $size75;
  line-height: 1.13;
  color: $white;

  @media (max-width: 1200px) {
    font-size: 45px;
  }
  @media (max-width: 850px) {
    font-size: 35px;
  }
  @media (max-width: 600px) {
    font-size: 25px;
  }
}

%h2 {
  font-family: 'Red Ring';
  font-style: normal;
  font-weight: 500;
  font-size: $size35;
  line-height: 1.35;
  color: $black;
}

%h3 {
  font-family: 'Red Ring';
  font-style: normal;
  font-weight: 500;
  font-size: $size25;
  line-height: 1.33;
  color: $black;
}

%big-text {
  font-family: 'Red Ring';
  font-style: normal;
  font-weight: normal;
  font-size: $size19;
  line-height: 1.31;
  color: $grey;

  @media (max-width: 850px) {
    font-size: 14px;
  }
  @media (max-width: 600px) {
    font-size: 12px;
  }
}

%regular-text {
  font-family: 'Red Ring';
  font-style: normal;
  font-weight: 300;
  font-size: $size13;
  line-height: 1.3;
  color: $black;
}

%medium-text {
  font-family: 'Red Ring';
  font-style: normal;
  font-weight: 300;
  font-size: $size15;
  line-height: 1.33;
  color: $grey;
}

%regular-bold {
  font-family: 'Red Ring';
  font-style: normal;
  font-size: $size13;
  line-height: 1.15;
  color: $black;
}

%medium-bold {
  font-family: 'Red Ring';
  font-style: normal;
  font-weight: 500;
  font-size: $size15;
  line-height: 1.33;
}

//--------------------------------------------------------------
//Extends class
//--------------------------------------------------------------

.btn {
  @extend %medium-bold;
  display: inline-block;

  min-width: 138px;
  padding: 14px 35px;

  color: $white;
  background: $green;

  border-radius: 5px;

  transition: all .2s;

  @media (max-width: 600px) {
    min-width: 50px;
    padding: 14px 15px;
  }

  &:hover {
    background: $sea;
    transition: all .2s;
  }
}

.btn-l {
  padding: 16px 34px 20px;

  @media (max-width: 600px) {
    padding: 15px 14px;
  }
}


// circle
// ===============================

.circle--top {
  position: absolute;
  border-radius: 50%;
  border-right: 1px solid $sea;
}
.circle--bottom {
  position: absolute;
  border-radius: 50%;
  border-left: 1px solid $sea;
}






















